import React, { useState } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames/bind';
import errorBoundary from '@src/components/errorBoundary';
import { useMaximize } from '@src/public/hooks/app';
import { message } from '@src/mobile/components/ModalBox';
import styles from './index.css';
import { postReport } from './service';
import { isGrounds, groundsClose } from '@src/public/utils/app';

const cx = classNames.bind(styles);

const Layout = ({ content }) => {
    const { params } = content;
    const { id } = params;

    const statusBarHeight = useMaximize();

    const handleBackClick = () => {
        if (isGrounds) {
            groundsClose();
        }
    };

    const reasonList = [
        { id: 1, text: '抄袭内容' },
        { id: 2, text: '虚假信息' },
        { id: 3, text: '垃圾营销' },
        { id: 4, text: '骚扰用户' },
        { id: 5, text: '其他' },
    ];

    const [reason, setReason] = useState();
    const [text, setText] = useState('');

    const handleTextChange = e => {
        const { value } = e.target;

        if (value.length <= 300) {
            setText(e.target.value);
        }
    };

    let allowSubmit = false;

    if (reason) {
        if (reason !== 5) {
            allowSubmit = true;
        } else {
            allowSubmit = !!text;
        }
    }

    const handleSubmit = async () => {
        if (allowSubmit) {
            // 提交数据
            try {
                await postReport(id, reason, text);
                message({
                    content: '举报成功',
                    onClose: () => {
                        // 关闭页面
                        if (isGrounds) {
                            groundsClose();
                        }
                    },
                });
            } catch (e) {
                message({ content: e });
                console.error(e);
            }
        }
    };

    return (
        <div className={styles.wrap}>
            <div className={styles.nav} style={{ paddingTop: statusBarHeight }}>
                <div className={styles.navContent}>
                    <div className={styles.navBack} onClick={handleBackClick} />
                    <div className={styles.navTitle}>举报</div>
                </div>
            </div>
            <div className={styles.content}>
                <div className={styles.reasonList}>
                    {reasonList.map(item => {
                        const { id, text } = item;

                        return (
                            <div
                                key={id}
                                className={cx('reason', { reasonChecked: id === reason })}
                                onClick={() => {
                                    setReason(id);
                                }}>
                                {text}
                            </div>
                        );
                    })}
                </div>
                <div className={styles.textarea}>
                    <textarea
                        value={text}
                        placeholder="请输入你想举报的内容（300字以内）"
                        onChange={handleTextChange}
                    />
                </div>
                <div className={cx('submit', { allowSubmit })} onClick={handleSubmit}>
                    提交
                </div>
            </div>
        </div>
    );
};

Layout.propTypes = {
    content: PropTypes.object,
};

export default errorBoundary(Layout);
